@charset "UTF-8";
@import url(https://fonts.googleapis.com/css?family=Raleway:200,300,400,600);
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, footer, main, menu, nav, output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline; }

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, footer, header, main, nav, section {
  display: block; }

body {
  line-height: 1; }

ol, ul {
  list-style: none; }

blockquote, q {
  quotes: none; }

blockquote:before, blockquote:after {
  content: '';
  content: none; }

q:before, q:after {
  content: '';
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

button {
  background-color: transparent;
  outline: none;
  border: 0;
  cursor: pointer; }

html {
  font-size: 85%; }
  html header {
    padding-top: 2em;
    padding-left: 2em;
    padding-right: 2em;
    padding-bottom: 0;
    margin-bottom: 1em; }

body {
  width: 100%;
  /*max-width: 1200px;*/
  height: auto;
  /* Location of the image */
  background-image: url("../../../assets/images/t0lh/homepage_1.png");
  /* Background image is centered vertically and horizontally at all times */
  background-position: center center;
  /* Background image doesn't tile */
  background-repeat: no-repeat;
  /* Background image is fixed in the viewport so that it doesn't move when 
	 the content's height is greater than the image's height */
  background-attachment: fixed;
  /* This is what makes the background image rescale based
	 on the container's size */
  background-size: cover;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
  /* Set a background color that will be displayed
	 while the background image is loading */
  background-color: #bdc8ca;
  color: #bdc8ca;
  font-family: "Raleway", Arial, "Helvetica Neue", Helvetica, sans-serif;
  line-height: 1.6; }

main {
  padding: 0.75em; }

section {
  padding: 0.75em; }

small {
  font-size: 10px;
  font-size: 1rem; }

a {
  text-decoration: none;
  color: #bdc8ca; }

nav {
  padding: 0.75em; }

nav li {
  padding: 0 25px; }

nav li a {
  font-weight: 600; }

h1 {
  font-size: 64.25px;
  font-size: 6.425rem; }

h2 {
  font-size: 42.368px;
  font-size: 4.2368rem; }

h3 {
  font-size: 39.71px;
  font-size: 3.971rem; }

h4 {
  font-size: 26.18px;
  font-size: 2.618rem; }

h5 {
  font-size: 24.54px;
  font-size: 2.454rem; }

h6 {
  font-size: 16.18px;
  font-size: 1.618rem; }

p {
  font-size: 16px;
  font-size: 1.6rem; }

