    overflow: hidden;
    background-color: #fff;
    text-align: center; }
  header nav li {
    padding: 4px 25px;
    letter-spacing: .1rem; }
  header nav li a {
    text-transform: uppercase;
    font-size: 9px;
    font-size: 0.9rem;
    display: block; }
  header .menu-icon-bar {
    background-color: #bdc8ca;
    opacity: 0.5;
    display: block;
    width: 22px;
    height: 2px;
    border-radius: 1px; }
  header .menu-icon-bar + .menu-icon-bar {
    margin-top: 4px; }
  header .menu {
    clear: both;
    max-height: 0;
    transition: max-height 1s ease-out;
    background-color: rgba(0, 0, 0, 0.8); }
  header .menu-icon {
    cursor: pointer;
    display: inline-block;
    float: right;
    position: relative;
    user-select: none; }
  header .menu-icon .navicon {
    background: #bdc8ca;
    display: block;
    height: 2px;
    position: relative;
    transition: background .2s ease-out;
    width: 18px; }
  header .menu-icon .navicon:before,
  header .menu-icon .navicon:after {
    background: #bdc8ca;
    content: '';
    display: block;
    height: 100%;
    position: absolute;
    transition: all .2s ease-out;
    width: 100%; }
  header .menu-icon .navicon:before {
    top: 5px; }
  header .menu-icon .navicon:after {
    top: -5px; }
  header .menu-btn {
    display: none; }
  header .menu-btn:checked ~ nav ul.menu {
    max-height: 240px;
    background-color: rgba(0, 0, 0, 0.8);
    border-radius: 5px; }
  header .menu-btn:checked ~ .menu-icon .navicon {
    background: transparent; }
  header .menu-btn:checked ~ .menu-icon .fa-moon-o {
    color: #26b2cb; }
  header .menu-btn:checked ~ .menu-icon .navicon:before {
    transform: rotate(-45deg); }
  header .menu-btn:checked ~ .menu-icon .navicon:after {
    transform: rotate(45deg); }
  header .menu-btn:checked ~ .menu-icon:not(.steps) .navicon:before,
  header .menu-btn:checked ~ .menu-icon:not(.steps) .navicon:after {
    top: 0; }
  @media (min-width: 48.625em) {
