header {
	
	text-shadow: 1px 1px rgba(0, 0, 0, 1);
	
	nav ul {		
		margin: 0;
		padding: 0;
		list-style: none;
		overflow: hidden;
		background-color: #fff;
		text-align: center;
	}

	nav li {
		padding: 4px 25px;
		letter-spacing: .1rem;
	}
	
	nav li a {
		text-transform: uppercase;
		@include font-size(0.9);
		display: block;	
	}
	
