	.menu-icon-bar {
		background-color: $jungle-mist;
    	opacity: 0.5;
		display: block;
		width: 22px;
		height: 2px;
		border-radius: 1px;
	}

	.menu-icon-bar + .menu-icon-bar {
		margin-top: 4px;
	}
	
	/*.open {
		display: block;	
		background-color: rgba(0, 0, 0, 0.8);
		margin-top: 5px;
    	border-radius: 5px;
	}*/
	
	.menu {
		clear: both;
		max-height: 0;
		transition: max-height 1s ease-out;
		background-color: rgba(0, 0, 0, 0.8);
	}
	
	/* Hamburger Menu */
	
	.menu-icon {
		cursor: pointer;
	  	display: inline-block;
		float: right;
		position: relative;
		user-select: none;
	}

	.menu-icon .navicon {
		  background: $jungle-mist;
		  display: block;
		  height: 2px;
		  position: relative;
		  transition: background .2s ease-out;
		  width: 18px;
	}

	.menu-icon .navicon:before,
	.menu-icon .navicon:after {
		  background: $jungle-mist;
		  content: '';
		  display: block;
		  height: 100%;
		  position: absolute;
		  transition: all .2s ease-out;
		  width: 100%;
	}

	.menu-icon .navicon:before {
	  	top: 5px;
	}

	.menu-icon .navicon:after {
	 	top: -5px;
	}
	
	.menu-btn {
		display: none;
	}
	
	.menu-btn:checked ~ nav ul.menu {
		max-height: 240px;
		background-color: rgba(0, 0, 0, 0.8);
    	border-radius: 5px;
	}

	.menu-btn:checked ~ .menu-icon .navicon {
	  	background: transparent;
	}
	
	.menu-btn:checked ~ .menu-icon .fa-moon-o {
	  	color: $summer-sky;
	}

	.menu-btn:checked ~ .menu-icon .navicon:before {
	  	transform: rotate(-45deg);
	}

	.menu-btn:checked ~ .menu-icon .navicon:after {
	  	transform: rotate(45deg);
	}

	.menu-btn:checked ~ .menu-icon:not(.steps) .navicon:before,
	.menu-btn:checked ~ .menu-icon:not(.steps) .navicon:after {
	  	top: 0;
	}


	@include for-large-screens($large-screen) {
		
		.t0-nav-group-toggle {
			display: none;
		}
		
		.menu {
			clear: none;
			float: right;
			max-height: none;
			background-color: transparent;
		}
		
		.menu-icon {
			display: none;
		}

		nav {
			overflow: auto;
			display: block;
		}
		
		nav ul {
			float: right;
		}

		nav li {
			display: inline-block;
			padding: 0 25px;
		}

	}
	
}